import * as playModule from "./pages/play";
import * as replayModule from "./pages/replay";

import * as cfg from "../config";
const delay = (delayInms) => {
    delayInms = 1000;
    return new Promise((resolve) => setTimeout(resolve, delayInms));
};

let _provider;
const _contracts = {
    progmon: null,
    progressNFT: null,
};

let _state;
let _updateState;

const setup = ({ state, updateState }) => {
    _state = state;
    _updateState = updateState;
};

const connectWallet = async () => {
    const rObject = {
        err: "",
    };

    if (window.tronWeb) {
        await window.tronLink.request({
            method: "tron_requestAccounts",
        });
        const accountAddr = window.tronWeb.defaultAddress.base58;
        if (accountAddr != null) {
            await initContractHandlers();
            rObject.account = accountAddr;
        } else {
            rObject.err = "No default account on TronLink wallet";
        }
    } else {
        rObject.err = "You need to connect a Tron Link Wallet";
    }
    console.log({ connectWallet: "called", rObject });

    return rObject;
};

const initContractHandlers = async () => {
    await delay(1000);

    _contracts.progmon = await window.tronWeb
        .contract()
        .at(cfg.BACK_CONSTANTS.progmonAddr);
    _contracts.progressNFT = await window.tronWeb
        .contract()
        .at(cfg.BACK_CONSTANTS.progressNFTAddr);
    console.log({ initContractHandlers: "called", _contracts });
    // await delay(1000);
};

const register = async () => {
    await delay(1000);
    await initContractHandlers();

    await _contracts.progmon.register().send();
    return { err: "", progressNFT_Id: null };
};

const getCurrentLevelInfo = async ({ progressNFT_Id }) => {
    await delay(1000);
    await initContractHandlers();
    return await playModule.getCurrentLevelInfo({ _contracts, progressNFT_Id });
};

const getReplayInfo = async ({ matchId }) => {
    await delay(3000);
    await initContractHandlers();
    return await replayModule.getReplayInfo({ _contracts, matchId });
};

const startBattle = async ({ progressNFT_Id, monsterId, sequences }) => {
    await initContractHandlers();

    return await playModule.startBattle({
        _contracts,
        progressNFT_Id,
        monsterId,
        sequences,
    });
};

const claimMonster = async ({ matchId }) => {
    await initContractHandlers();

    return await replayModule.claimMonster({
        _provider,
        _contracts,
        matchId,
    });
};

const getProgressionInfo = async () => {
    await delay(1000);
    await initContractHandlers();

    const progressNFT_tokenCounter = parseInt(
        (await _contracts.progressNFT.tokenCounter().call()).toString()
    );

    console.log({ progressNFT_tokenCounter });

    const info = { tracks: [] };

    for (
        let progressNFT_Id = 0;
        progressNFT_Id < progressNFT_tokenCounter;
        ++progressNFT_Id
    ) {
        const nftOwner = window.tronWeb.address.fromHex(
            await _contracts.progressNFT.ownerOf(progressNFT_Id).call()
        );

        console.log(nftOwner);

        if (nftOwner == _state.account) {
            info.tracks.push({
                progressNFT_Id,
                levelInfo: await getCurrentLevelInfo({
                    progressNFT_Id,
                }),
            });
        }
    }

    return {
        err: "",
        info,
    };
};

export {
    setup,
    initContractHandlers,
    connectWallet,
    register,
    getCurrentLevelInfo,
    getReplayInfo,
    startBattle,
    claimMonster,
    getProgressionInfo,
};
