import * as cfg from "../../config";

import { _buildSkill } from "./play";

const getReplayInfo = async ({ _contracts, matchId }) => {
    console.log({ _contracts, matchId });

    const replayInfo = await _contracts.progmon.viewPlayOut(matchId).call();
    const matchInfo = await _contracts.progmon.matches(matchId).call();

    console.log({ replayInfo, matchInfo });

    const { monster0_Id, monster1_Id } = matchInfo;

    const monsterIds = [
        parseInt(monster0_Id.toString()),
        parseInt(monster1_Id.toString()),
    ];

    const {
        canClaim,
        monster0_health,
        monster1_health,
        isFinal,
        skillId,
        turn,
    } = replayInfo;
    const moves = [
        {
            id: 0,
            state: {
                monster0: { health: monster0_health[0].toString() },
                monster1: { health: monster0_health[1].toString() },
            },
            action: {
                monster: 2,
                effect: "dummy",
                damage: -100,
            },
        },
    ];

    console.log({ monster0_health, turn, skillId });

    let skills = cfg.FRONT_CONSTANTS.skillImgPaths.map(
        async (e, k) =>
            await _buildSkill({
                _contracts,
                id: k,
            })
    );

    skills = await Promise.all(skills);

    console.log({ skills });

    for (let i = 1; i < 100; ++i) {
        const { name, damage } = skills[skillId[i]];
        const move = {
            id: i,
            state: {
                monster0: { health: monster0_health[i].toString() },
                monster1: {
                    health: monster1_health[i].toString(),
                },
            },
            action: {
                monster: parseInt(turn[i].toString()),
                effect: name,
                damage,
            },
        };
        moves.push(move);
        if (isFinal[i]) {
            break;
        }
    }
    console.log({ moves });
    return {
        isPending: false,
        canClaim,
        monsterIds,
        moves,
    };
};

const claimMonster = async ({ _provider, _contracts, matchId }) => {
    const signer = _provider.getSigner();
    await _contracts.progmon.connect(signer).claim(matchId);
};

export { getReplayInfo, claimMonster };
